import React from 'react';
import * as XLSX from 'xlsx';

function DataManager({ products, orders, onImport, onClear }) {
  // 
  const handleCostImport = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(worksheet, {
          raw: false,
          dateNF: 'yyyy-mm-dd',
          defval: ''
        });

        console.log(':', jsonData);

        const processedProducts = jsonData.map((row, index) => ({
          id: String(index + 1),
          date: row[''] || new Date().toISOString().split('T')[0],
          name: row[''] || '',
          costPrice: parseFloat(row['']) || 0,
          promotionFee: parseFloat(row['']) || 0,
          description: row[''] || '',
          stock: Number(row['']) || 0,
          stockAlert: Number(row['']) || 0,
          supplier: row[''] || '',
          location: row[''] || ''
        }));

        console.log(':', processedProducts);

        onImport({
          products: processedProducts,
          orders: orders // 
        });

        e.target.value = '';
        alert('');
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    };

    reader.readAsArrayBuffer(file);
  };

  // 
  const handleOrderImport = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (e) => {
      try {
        const data = new Uint8Array(e.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];

        const jsonData = XLSX.utils.sheet_to_json(worksheet, {
          raw: false,
          dateNF: 'yyyy-mm-dd',
          defval: ''
        });

        console.log(':', jsonData);

        const processedOrders = jsonData.map((row, index) => {
          // 
          const product = products.find(p => p.name === row['']);
          const date = row[''] ? new Date(row['']).toISOString().split('T')[0] : new Date().toISOString().split('T')[0];
          const quantity = parseInt(row['']) || 1;
          const sellingPrice = parseFloat(row['']) || 0;
          const shippingFee = parseFloat(row['']) || 0;

          const order = {
            id: String(index + 1).padStart(4, '0'),
            date: date,
            items: [{
              productId: product ? product.id : '',
              quantity: quantity,
              sellingPrice: sellingPrice
            }],
            shippingFee: shippingFee
          };

          // 
          const costPrice = product ? product.costPrice : 0;
          const promotionFee = product ? product.promotionFee : 0;
          order.profit = (sellingPrice * quantity) - (costPrice * quantity) - shippingFee - promotionFee;

          console.log(':', order);
          return order;
        });

        console.log(':', processedOrders);

        onImport({
          products: products,
          orders: processedOrders
        });

        e.target.value = '';
        alert('');
      } catch (error) {
        console.error(':', error);
        alert('');
      }
    };

    reader.readAsArrayBuffer(file);
  };

  // 
  const handleCostExport = () => {
    try {
      const exportData = products.map(product => ({
        '': product.date,
        '': product.name,
        '': product.costPrice,
        '': product.promotionFee,
        '': product.description,
        '': product.stock || 0  // 
      }));

      const wb = XLSX.utils.book_new();
      const ws = XLSX.utils.json_to_sheet(exportData, {
        header: ['', '', '', '', '', '']
      });

      // 
      const colWidths = [
        { wch: 15 }, // 
        { wch: 20 }, // 
        { wch: 10 }, // 
        { wch: 10 }, // 
        { wch: 20 }, // 
        { wch: 10 }  // 
      ];
      ws['!cols'] = colWidths;

      XLSX.utils.book_append_sheet(wb, ws, "");
      XLSX.writeFile(wb, ".xlsx");
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  // 
  const handleOrderExport = () => {
    try {
      const exportData = orders.map(order => {
        const product = products.find(p => p.id === order.items[0].productId);
        return {
          '': order.date,
          '': product ? product.name : '',
          '': order.items[0].quantity,
          '': order.items[0].sellingPrice,
          '': order.shippingFee,
          '': order.profit
        };
      });

      // 
      const wb = XLSX.utils.book_new();
      const ws = XLSX.utils.json_to_sheet(exportData, {
        header: ['', '', '', '', '', '']
      });

      // 
      const colWidths = [
        { wch: 15 }, // 
        { wch: 20 }, // 
        { wch: 10 }, // 
        { wch: 10 }, // 
        { wch: 10 }, // 
        { wch: 10 }  // 
      ];
      ws['!cols'] = colWidths;

      XLSX.utils.book_append_sheet(wb, ws, "");
      XLSX.writeFile(wb, ".xlsx");
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  return (
    <div className="data-manager">
      <div className="import-section">
        <h3></h3>
        <div>
          <input
            type="file"
            accept=".xlsx,.xls"
            onChange={handleCostImport}
            className="file-input"
          />
          <p className="help-text"></p>
        </div>
        <div>
          <input
            type="file"
            accept=".xlsx,.xls"
            onChange={handleOrderImport}
            className="file-input"
          />
          <p className="help-text"></p>
        </div>
      </div>

      <div className="export-section">
        <h3></h3>
        <button onClick={handleCostExport} className="export-button">
          
        </button>
        <button onClick={handleOrderExport} className="export-button">
          
        </button>
        <button onClick={onClear} className="export-button clear-button">
          
        </button>
      </div>
    </div>
  );
}

export default DataManager; 